import React, { useState } from 'react';
import styled from 'styled-components';
import Modal from 'react-modal';
import Button from './Button';
import Slider from 'react-slick';

const customStyles = {
  overlay: {
    backgroundColor: 'rgb(100 100 100 / 85%)',
    zIndex: '999',
  },
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
    width: '70%',
    height: '85%',
  },
};

const CloseDiv = styled.div`
  text-align: right;
  .closeButton {
    cursor: pointer;
    &:hover {
      background: #ebebeb;
    }
  }
`;

const ContentDiv = styled.div`
  font-size: 1.1rem;
  overflow: auto;
  height: 92%;
  .titleDiv {
    font-size: 2rem;
    font-weight: 800;
    padding: 0px 0px 0px 15px;
    border-left: 5px solid #ffb100;
  }
  .subTitleDiv {
    color: #a1a1a1;
    padding: 0px 0px 0px 15px;
    border-left: 5px solid #ffb100;
  }
  .skill {
    .skillTitle {
      font-size: 1.2rem;
      font-weight: 800;
    }
    .skillArea {
      border: 1px solid #e7e7e7;
      border-radius: 15px;
      padding: 0px 0px 20px 0px;
      margin-top: 30px;
      min-height: 90px;
      overflow: auto;

      .skillBlock {
        border: 1px solid #e7e7e7;
        background: #e7e7e7;
        border-radius: 8px;
        width: 140px;
        height: 50px;
        float: left;
        margin: 20px 0px 0px 15px;
        text-align: center;
        align-items: center;
        display: grid;
      }
    }
  }
  .linkArea {
    margin-top: 30px;
    .linkTitle {
      font-weight: 700;
      margin-right: 50px;
    }
    .link {
      color: #a3a3a3;
    }
  }
  .imgAndContents {
    min-height: 100px;
    overflow: hidden;
    .imgDiv {
      //   border: 1px solid black;
      float: left;
      width: 49%;
      border-right: 1px solid #e7e7e7;
      //   padding: 0px 0px 0px 15px;
    }
    .contentsDiv {
      //   border: 1px solid black;
      float: left;
      width: 49%;
      padding: 0px 0px 0px 65px;
      color: #838383;
    }
    .verticalDiv {
      border-right: 2px solid #e7e7e7;
      float: left;
      //   width: 10%;
    }
  }
`;

const LineDiv = styled.div`
  margin-top: 30px;
  margin-bottom: 30px;
  border-bottom: 1px solid #e7e7e7;
`;

const LineVertical = styled.div`
  //   margin-left: 20px;
  //   margin-right: 20px;
  border-right: 2px solid #e7e7e7;
  //   width: 10%;
  float: left;
  height: 100%;
`;

const Bottom = styled.div`
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translate(-50%, 0%);
`;

const SliderCss = styled(Slider)`
  width: 80%;
  left: 50%;
  transform: translate(-50%);

  .container {
    width: 90%;
    height: 500px;
    margin: 5%;
  }

  .slick-slide {
    height: 320px;
    box-sizing: border-box;
  }

  .slick-slide img {
    object-fit: cover;
    height: 500px;
    width: 100%;
    box-sizing: border-box;
  }

  .slick-dots {
    bottom: 0px;
  }

  .slick-prev {
    left: 0px;
    z-index: 1;
  }

  .slick-next {
    right: 10px;
    z-index: 1;
  }

  .slick-prev:before,
  .slick-next:before {
    color: #ffb100;
    font-size: 30px;
  }

  //   .slick-dots li button:before {
  //     color: #ffb100;
  //   }

  .sliderContentDiv {
    .projectDiv {
      //   box-shadow: 0px 5px 5px rgb(0 0 0 / 30%);
      border: 1px solid #e9e9e9;

      .projectImg {
        width: 100%;
        height: 100%;
        display: inline-block;
        overflow: hidden;
        .projectImg_img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
  }

  & div {
    height: 100%;
  }
`;

const NextPageButton = (props) => {
  const [modalIsOpen, setModalIsOpen] = React.useState(true);

  const settings = {
    slide: 'div',
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: true,
    autoplay: false,
    autoplaySpeed: 10000,
    pauseOnHover: true,
    draggable: true,
  };

  function afterOpenModal() {
    // references are now sync'd and can be accessed.
    // subtitle.style.color = '#f00';
  }

  function closeModal() {
    setModalIsOpen(false);
    props.callback();
  }

  return (
    <div>
      <Modal isOpen={modalIsOpen} onAfterOpen={afterOpenModal} onRequestClose={closeModal} style={customStyles} contentLabel="Modal">
        <CloseDiv>
          <img className="closeButton" src="./assets/images/closeicon32.png" alt="" onClick={closeModal} />
        </CloseDiv>
        <ContentDiv>
          <div className="titleDiv">{props.contents.title}</div>
          <div className="subTitleDiv">{props.contents.subTitle}</div>
          <LineDiv />
          <div className="imgAndContents">
            {props.contents.imgPath.length > 0 ? (
              <div className="imgDiv">
                <SliderCss {...settings}>
                  {props.contents.imgPath.map((v, i) => {
                    return (
                      <div className="sliderContentDiv">
                        <div className="projectDiv">
                          <div className="projectImg">
                            <img className="projectImg_img" src={`${v}`} alt="" />
                          </div>
                        </div>
                      </div>
                    );
                  })}
                </SliderCss>
              </div>
            ) : null}
            <div className="contentsDiv">{props.contents.contents}</div>
          </div>
          <LineDiv />
          <div className="skill">
            <div className="skillTitle">
              <ul>
                <li>사용 기술</li>
              </ul>
            </div>
            <div className="skillArea">
              {props.contents.skill.map((value, index) => (
                <div className="skillBlock" key={index}>
                  {value}
                </div>
              ))}
            </div>
          </div>
          <div className="linkArea">
            {props.contents.homepageUri ? (
              <>
                <span className="linkTitle">
                  <ul>
                    <li>Hompage</li>
                  </ul>
                </span>
                <span className="link">
                  <a href={props.contents.homepageUri.indexOf('http') !== -1 ? props.contents.homepageUri : 'javascript:void(0);'} target="_blank" rel="noreferrer">
                    {props.contents.homepageUri}
                  </a>
                </span>
              </>
            ) : null}
            {props.contents.gitUri ? (
              <>
                <span className="linkTitle">
                  <ul>
                    <li>Github</li>
                  </ul>
                </span>
                <span className="link">
                  <a href={props.contents.gitUri} target="_blank" rel="noreferrer">
                    {props.contents.gitUri}
                  </a>
                </span>
              </>
            ) : null}
          </div>
        </ContentDiv>
        <Bottom>
          <span>
            <Button onClick={closeModal}>CLOSE</Button>
          </span>
        </Bottom>
      </Modal>
    </div>
  );
};

export default NextPageButton;
