import React from 'react';
import styled, { css } from 'styled-components';
import palette from '../../lib/styles/palette';
import { media } from '../../lib/styles/media';

const backgroundStyle = css`
  min-height: 1041px;
  height: 100vh;
  width: 100%;
  background: ${palette.gray[10]};
  border: none;
  // padding: 5rem 5rem;
  outline: none;
  // display: grid;
  // align-items: center;
  & > .navArea {
    // border: 1px solid black;
    min-height: 83px;
    height: 8vh;
  }
  & > .top {
    // border: 1px solid black;
    min-height: 62px;
    height: 6vh;
    text-align: center;
    font-size: 3rem;
    font-weight: 300;
    color: #c9c9c9;
  }
  & > .middle {
    // border: 1px solid black;
    min-height: 833px;
    height: 80vh;
  }
  & > .bottom {
    // border: 1px solid black;
    min-height: 62px;
    height: 6vh;
  }
  ${(props) =>
    props.color &&
    css`
      background: ${props.color};
    `}

  ${media.desktop`
    height: 120vh;
    & > .middle {
      height: 100vh;
    }
  `}

  ${media.tablet`
    height: 120vh;
    & > .middle {
      height: 100vh;
    }
  `}

  ${media.phone`
    height: 120vh;
    & > .middle {
      height: 100vh;
    }
  `}
`;

const StyledMainDiv = styled.div`
  ${backgroundStyle}
`;

const Background = (props) => {
  return (
    <StyledMainDiv {...props}>
      <div className="navArea"></div>
      <div className="top">{props.children[0]}</div>
      <div className="middle">
        <div>{props.children[1]}</div>
      </div>
      <div className="bottom">{props.children[2]}</div>
    </StyledMainDiv>
  );
};

export default Background;
