import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import Responsive from './Responsive';
import { Link } from 'react-scroll';

const Wrapper = styled(Responsive)`
  position: fixed;
  bottom: 0;
  height: 5rem;
  width: fit-content;
  float: right;
  right: 0px;
  margin: 15px;
`;

const NavLinks = styled(Link)`
  .scrollTop {
    display: block;
    width: 48px;
    height: 48px;
    // border: 1px solid black;
    cursor: pointer;
    & img {
      &:hover {
        width: 50px;
        height: 50px;
      }
    }
  }
`;

const Header = () => {
  const [scroll, setScroll] = useState(false);

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const handleScroll = () => {
    if (window.scrollY > 0) {
      setScroll(true);
    } else {
      setScroll(false);
    }
  };

  return (
    <>
      {scroll ? (
        <Wrapper>
          <NavLinks activeClass="active" className="Home" to="Home" spy={true} smooth={true} duration={500}>
            <span className="scrollTop">
              {' '}
              <img className="github" src="./assets/images/arrowupicon48.png" alt="" />
            </span>
          </NavLinks>
        </Wrapper>
      ) : null}
    </>
  );
};

export default Header;
