import React from 'react';
import styled from 'styled-components';
import Responsive from './Responsive';
import Button2 from './Button2';
import { Link } from 'react-scroll';
import palette from '../../lib/styles/palette';

const HeaderBlock = styled.div`
  position: fixed;
  width: 100%;
  background: white;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.08);
  z-index: 998;
`;

const Wrapper = styled(Responsive)`
  width: 100%;
  height: 4rem;
  align-items: center;
  display: flex;
  justify-content: space-between;
  .logo {
    padding: 0px 0px 0px 10px;
    border-left: 5px solid #ffb100;
    margin-left: 40px;
    font-size: 1.2rem;
    font-weight: 800;
    letter-spacing: 2px;
  }
  .right {
    // display: flex;
    align-items: center;
  }
`;

const Spacer = styled.div`
  height: 4rem;
`;

const NavLinks = styled(Link)`
  // display: inline-block;
  // width: 100%;
  color: #adadad;

  &.active {
    color: ${palette.yellow[2]};
  }
`;

const NavButtonSpan = styled.span`
  // height: 100%;
  & > a {
    margin: 30px;
    font-weight: 600;
    font-size: 1.05rem;
    cursor: pointer;
    &:hover {
      // background: red;
    }
  }
`;

const SNSButtons = styled.span`
  margin-right: 30px;
  margin-left: 20px;
  & > a {
    margin: 10px;
    font-weight: 600;
    font-size: 1.05rem;
    cursor: pointer;
  }
`;

const LineSpan = styled.span`
  border-right: 2px solid #cfcfcf;
`;

const StyledButton2 = styled(Button2)`
  width: 100px;
  height: 50px;
`;

const ForddIcon = styled.span`
  width: 38px;
  height: 30px;
  display: inline-block;
  overflow: hidden;
  position: relative;
  top: 8px;
  // margin: 0 100px;
  // box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.08);
  .fordd {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
`;

const GithubIcon = styled.span`
  position: relative;
  top: 7px;
  .github {
  }
`;

const Header = () => {
  return (
    <>
      <HeaderBlock>
        <Wrapper>
          <span>
            <Link to="/" className="logo">
              DAEHWI PARK
            </Link>
          </span>

          <NavButtonSpan>
            <StyledButton2>
              <NavLinks
                activeClass="active"
                className="Home"
                to="Home"
                spy={true}
                smooth={true}
                // offset={5}
                duration={500}
              >
                <span>Home</span>
              </NavLinks>
            </StyledButton2>
            <StyledButton2>
              <NavLinks
                activeClass="active"
                className="About"
                to="About"
                spy={true}
                smooth={true}
                // offset={5}
                duration={500}
              >
                <span>About</span>
              </NavLinks>
            </StyledButton2>
            <StyledButton2>
              <NavLinks
                activeClass="active"
                className="Portfolio"
                to="Portfolio"
                spy={true}
                smooth={true}
                // offset={5}
                duration={500}
              >
                <span>Portfolio</span>
              </NavLinks>
            </StyledButton2>
            <StyledButton2>
              <NavLinks
                activeClass="active"
                className="Contact"
                to="Contact"
                spy={true}
                smooth={true}
                // offset={5}
                duration={500}
              >
                <span>Contact</span>
              </NavLinks>
            </StyledButton2>
            <LineSpan />
            <SNSButtons>
              <a href="https://github.com/ya4216" target="_blank" rel="noreferrer">
                <GithubIcon>
                  <img className="github" src="./assets/images/githubicon32.png" alt="" />
                </GithubIcon>
              </a>

              <a href="https://fordd-dev.netlify.app/" target="_blank" rel="noreferrer">
                <ForddIcon>
                  <img className="fordd" src="./assets/images/forddicon.png" alt="" />
                </ForddIcon>
              </a>
            </SNSButtons>
          </NavButtonSpan>
        </Wrapper>
      </HeaderBlock>
    </>
  );
};

export default Header;
