import React from 'react';
import Background from '../components/common/Background';
import styled from 'styled-components';
import Button from '../components/common/Button';
import { Link } from 'react-scroll';
import NextPageButton from '../components/common/NextPageButton';
import { media } from '../lib/styles/media';

const HomeDiv = styled.div`
  text-align: center;
  font-size: 1.3rem;
  height: 100%;
  display: grid;
  align-content: center;
  transform: translate(0%, 10%);

  .myName {
    font-weight: bold;
    font-size: 1.5rem;
    margin-top: 10px;
  }

  .myNameEng {
    font-size: 1rem;
    font-weight: 500;
    font-family: emoji;
  }

  .position {
    font-weight: bold;
    font-size: 3rem;
    color: #565656;
    // background: #ffb100;
    // border-top: 5px solid black;
    // border-bottom: 5px solid black;
    font-family: fantasy;
    margin: 30px 0px 30px 0px;
  }

  .myInfo {
    font-size: 1.2rem;
    font-weight: 500;
    color: #767676;
  }

  .goContact {
    font-weight: bold;
    font-size: 1.5rem;
    margin-top: 50px;

    ${media.desktop`
        margin-top: 10px;
    `}

    ${media.tablet`
        margin-top: 0px;
    `}

    ${media.phone`
        margin-top: 0px;
    `}
  }
`;

const PhotoDiv = styled.div`
  width: 350px;
  height: 350px;
  display: inline-block;
  overflow: hidden;
  margin: 0 auto;
  border-radius: 175px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.08);
  .profileImg {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
`;

const Home = () => {
  return (
    <div id="Home">
      <Background>
        <div className="title"></div>
        <HomeDiv>
          <PhotoDiv>
            <img className="profileImg" src="./assets/images/parkdaehwi.jpg" alt=""></img>
          </PhotoDiv>
          <div className="myName">박대휘</div>
          <div className="myNameEng">DAEHWI PARK</div>
          <div className="position">Web Front-end Developer</div>
          <div className="myInfo">
            안녕하세요, 만 4년의 프론트엔드 개발 경험을 가진
            <br />웹 프론트엔드 개발자 박대휘 입니다.
          </div>
          <div className="goContact">
            <Link activeClass="active" className="Contact" to="Contact" spy={true} smooth={true} duration={500}>
              <Button>CONTACT ME!</Button>
            </Link>
          </div>
        </HomeDiv>
        <NextPageButton className="About" to="About">
          NEXT PAGE
        </NextPageButton>
      </Background>
    </div>
  );
};

export default Home;
