import React, { useEffect, useState } from 'react';
import Background from '../components/common/Background';
import styled from 'styled-components';
import { Link } from 'react-scroll';
import Button from '../components/common/Button';
import NextPageButton from '../components/common/NextPageButton';

const HomeDiv = styled.div`
  text-align: center;
  font-size: 1.3rem;
  height: 100%;
  display: grid;
  align-content: center;
  transform: translate(0%, 10%);
`;

const MainDiv = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  span .downloadButton {
    // text-align: right;
    margin-top: 60px;
  }
  span {
    .profileFirst {
      font-size: 1.5rem;
    }
    .profileSecond {
      font-weight: 100;
      font-size: 1.1rem;
      color: #606060;
    }
    .profileThird {
    }
  }
`;

const ProfileImg = styled.span`
  width: 450px;
  height: 650px;
  display: inline-block;
  overflow: hidden;
  margin: 0 100px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.08);
  .profileImg {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
`;

const LineHorizontal = styled.div`
  margin-top: 30px;
  margin-bottom: 30px;
  border-bottom: 1px solid #e7e7e7;
`;

const StyledTable = styled.table`
  // border: 1px solid #444444;
  width: 100%;
  th,
  td {
    width: 20%;
    // background: gray;
    // border-radius: 15px;
    // border: 1px solid #444444;
  }
`;

const StyledTd = styled.div`
  background: #f3f3f3;
  border-radius: 10px;
  height: 60px;
  align-items: center;
  display: grid;
`;

const About = () => {
  const [size, setSize] = useState({
    width: 0,
    height: 0,
  });

  useEffect(() => {
    const handleResize = () => {
      setSize({
        width: window.innerWidth,
        height: window.innerHeight,
      });
    };
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  return (
    <div id="About">
      <Background color="white">
        <div className="title">ABOUT ME</div>
        <HomeDiv>
          <MainDiv>
            {window.innerWidth > 768 ? (
              <ProfileImg>
                <img className="profileImg" src="./assets/images/parkdaehwi.jpg" alt="" />
              </ProfileImg>
            ) : null}
            <span>
              <div className="profileFirst">안녕하세요, 웹 프론트엔드 개발자 박대휘 입니다.</div>
              <LineHorizontal />
              <div className="profileSecond">
                저는 만 4년의 프론트엔드 개발 경력을 가지고 있는 개발자 입니다.
                <br />
                SKTelecom과 LG전자의 프로젝트에 참여한 경험이 있고, 개인 프로젝트로
                <br />
                4DD(For Dreaming Developers) 라는 개발자를 꿈꾸는 사람들을 위해
                <br />
                PC와 스마트폰 에서 간편하게 보고 개발에 대한 여러가지 지식을 익힐 수 있도록
                <br />
                텍스트 기반의 개발 강의를 제공하는 개발강의 플랫폼을 만들었습니다.
              </div>
              <LineHorizontal />
              <div className="profileThird">
                <StyledTable>
                  <thead>
                    <tr>
                      <td colSpan={5}>My Skill</td>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>
                        <StyledTd>JavaScript</StyledTd>
                      </td>
                      <td>
                        <StyledTd>HTML5</StyledTd>
                      </td>
                      <td>
                        <StyledTd>CSS</StyledTd>
                      </td>
                      <td>
                        <StyledTd>Styled-Components</StyledTd>
                      </td>
                      <td>
                        <StyledTd>SCSS</StyledTd>
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <StyledTd>React</StyledTd>
                      </td>
                      <td>
                        <StyledTd>Redux</StyledTd>
                      </td>
                      <td>
                        <StyledTd>TypeScript</StyledTd>
                      </td>
                      <td>
                        <StyledTd>Node.js</StyledTd>
                      </td>
                      <td>
                        <StyledTd>Express</StyledTd>
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <StyledTd>MongoDB</StyledTd>
                      </td>
                      <td>
                        <StyledTd>vite</StyledTd>
                      </td>
                      <td>
                        <StyledTd>Git</StyledTd>
                      </td>
                      <td>
                        <StyledTd>JAVA</StyledTd>
                      </td>
                      <td>
                        <StyledTd>jQuery</StyledTd>
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <StyledTd>Spring</StyledTd>
                      </td>
                      <td>
                        <StyledTd>mysql</StyledTd>
                      </td>
                      <td>
                        <StyledTd>oracle</StyledTd>
                      </td>
                      <td>
                        <StyledTd></StyledTd>
                      </td>
                      <td>
                        <StyledTd></StyledTd>
                      </td>
                    </tr>
                  </tbody>
                </StyledTable>
              </div>
              <div className="downloadButton">
                <a href="./assets/images/parkdaehwi.jpg" download>
                  <Button>이력서 다운로드</Button>
                </a>
              </div>
            </span>
          </MainDiv>
        </HomeDiv>
        <NextPageButton className="Portfolio" to="Portfolio">
          NEXT PAGE
        </NextPageButton>
      </Background>
    </div>
  );
};

export default About;
